.icon-picker-container {
	display: none;
	width: 230px;
	z-index: 500;
	font-size: 10px;
	position: absolute;
	background: transparent;
}

.icon-picker-container .ui-tabs .ui-tabs-panel {
	padding: 0;
}

.icon-picker-container .ui-widget-content {
	border: 0;
}

.icon-picker-container .tab-content {
	height: 400px;
}

.icon-picker-container .icons-bottom-tabs {
	border: 1px solid #ccc;
}

.icon-picker-container .icons-bottom-tabs .ui-tabs-nav {
	clear: left;
	padding: 0 0.2em 0.2em 0.2em;
}

.icon-picker-container .icons-bottom-tabs .ui-tabs-nav li {
	top: auto;
	bottom: 0;
	margin: 0 0.2em 1px 0;
	border-top: 0;
}

.icon-picker-container .icons-bottom-tabs .ui-tabs-nav .ui-tabs-active {
	margin-top: -1px;
	padding-top: 1px;
}

.icon-picker-container .icon-filter {
	margin: 2px auto;
	width: 150px;
}

.icon-picker-container .icon-filter .search {
	border-radius: 4px;
	width: 150px;
}

.icon-picker-container .preview-container {
	float: left;
	text-align: center;
	font-size: 100%;
	margin: 10px 10px 0 10px;
	padding: 3px;
	border: 1px solid #ccc;
	transform-style: preserve-3d;
}

.icon-picker-container .preview-container i {
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.icon-picker-container .preview {
	overflow: hidden;
	width: 55px;
	height: 50px;
}

.icon-picker-container .actions-container {
	margin: 10px;
}

.icon-picker-container .actions-container .action-btn {
	display: block;
	margin: 10px 0;
	text-align: center;
	padding: 10px;
	background-color: #6c60ba;
	color: #fff;
	font-weight: bold;
	overflow: hidden;
	text-decoration: none;
	border-radius: 4px;
}

.icon-picker-container .actions-container .action-btn:hover {
	background-color: #9e2a81;
}

.icon-picker-container .icons-container {
	border: 1px solid #ccc;
	text-align: center;
	position: relative;
	width: auto;
}

.icon-picker-container .icons {
	position: relative;
	height: 150px;
	overflow: hidden;
	overflow-y: auto;
}

.icon-picker-container .icons p {
	clear: left;
	font-weight: bold;
	margin: 20px 4px;
	font-size: 14px;
	color: #c3f;
}

.icon-picker-container .icons a {
	width: 28px;
	height: 32px;
	line-height: 32px;
	font-size: 14px;
	float: left;
	display: block;
}

.icon-picker-container .icons a:hover {
	background-color: #fbf4f4;
	color: blue;
}

.icon-picker-container .icons a:hover :before {
	font-size: 28px;
	vertical-align: -5px;
	color: blue;
}

.icon-picker-container .icons .icon-selected {
	color: #ffffff;
	background-color: #21b384;
}

.icon-picker-container .cats-container {
	position: relative;
	clear: both;
}

.icon-picker-container .cats-container a {
	padding: 4px;
	margin: 2px 0;
	font-weight: bold;
	font-size: 10px;
	background-color: #ccc;
	border-radius: 4px;
	position: relative;
	display: inline-block;
	width: auto;
}

.icon-picker-container fieldset {
	border: 0;
	margin: 8px;
}

.icon-picker-container fieldset dt {
	width: 30%;
}

.icon-picker-container fieldset dd {
	margin-left: 31%;
}

.icon-picker-container ul {
	margin: 0;
	padding: 0;
}

.icon-picker-container ul li {
	display: inline;
}

.icon-picker-container ul li .icons-color-container {
	position: relative;
	display: inline-block;
	border: 1px solid #ccc;
	cursor: pointer;
	padding: 1px;
}

.icon-picker-container ul li .selected {
	border: 2px solid #333;
}

.icon-picker-container ul li .icons-color-container .color {
	display: block;
	font-size: 12px;
	width: 13px;
}

.icon-picker-container ul li input {
	display: none;
}

.icon-picker-container .icons-drop {
	border: 1px solid #ccc;
	border-bottom: 0;
	margin-bottom: -5px;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
	z-index: 5000;
}

.icon-picker-container .bg-blue {
	background-color: #0000ff;
}

.icon-picker-container .bg-gray {
	background-color: #9b9595;
}

.icon-picker-container .bg-green {
	background-color: #90af45;
}

.icon-picker-container .bg-orange {
	background-color: #ffa500;
}

.icon-picker-container .bg-purple {
	background-color: #820182;
}

.icon-picker-container .bg-red {
	background-color: #ff0000;
}